@import './../mixins.scss';

.form {
    color: #fff;
    margin-top: 50px;
    padding: 25px 35px 0px 35px;
    &__container {
        height: 970px;
        padding: 45px 20px 65px 20px;
        border-radius: 40px;
        background: radial-gradient(50% 50% at 50% 50%, #403A5F 0%, #211E2E 100%);
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
        .form__title {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            gap: 20px;
            h2 {
                text-align: center;
                font-weight: 700;
                font-size: 24px;
                line-height: 29px;
                position: relative;

                &::after{
                    position: absolute;
                    content: '_____________';
                    font-size: 19px;
                    top: 110%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    color: #C0B7E8;
                }
                }
            h3 {
                font-weight: 300;
                font-size: 24px;
                line-height: 29px;
                text-align: center;
            }
        }
    }
    .form__main{
        margin-top: 35px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        gap: 30px;
        textarea{
            width: 100%;
            min-height: 220px;
            border: 2px solid #fff;
            border-radius: 40px;
            background: transparent;
            color: #fff;
            padding: 28px 40px;
            resize: none;
            &::placeholder{
                color: #fff;
            }
        }
        .form__button {
            margin: 20px auto;
            padding: 16px 40px;
            height: 48px;
            width: 100%;
            background: linear-gradient(90deg, #8176AF 0%, #C0B7E8 100%);
            border-radius: 40px;
            font-weight: 700;
            font-size: 14px;
            line-height: 17px;
            color: #343045;
            border: none;
            cursor: pointer;
            &:hover {
                border: 2px solid #fff;
                color: #fff;
            }
        }
    }

    @include respond-to(large) {
        margin-top: 100px;
        padding: 0px 80px 0px 85px;
    
        &__container {
            height: 1010px;
            border-radius: 100px;
            background: radial-gradient(50% 50% at 50% 50%, #403A5F 0%, #211E2E 100%);
            padding: 85px 110px;
    
            .form__title {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                gap: 30px;
                h2 {
                    font-size: 36px;
                    font-weight: 700;
                    line-height: 44px;
                    position: relative;
    
                    &::after{
                        position: absolute;
                        content: url(../img/line/line-form.png);
                        top: 50%;
                        left: -50%;
                        transform: translateX(8%);
                    }
                    }
                h3 {
                    font-size: 36px;
                    font-weight: 300;
                    line-height: 44px;
                    text-align: center;
                }
            }
    
            .form__main{
                margin-top: 65px;
                display: grid;
                grid-template-columns: repeat(2, 1fr);
                grid-template-rows: repeat(5, 1fr);
                gap: 10px;
    
                .form__tell{
                    grid-row: 4 / span 2;
                    grid-column: 1 / span 2;
    
                }

                textarea{
                    width: 100%;
                    min-height: 210px;
                    border: 2px solid #fff;
                    border-radius: 40px;
                    height: 72px;
                    background: transparent;
                    color: #fff;
                    padding: 28px 40px;
                    resize: none;
    
                    &::placeholder{
                        color: #fff;
                    }
    
                }
    
                .form__button {
                    grid-column: 1 / span 2;
                    margin: 50px auto 0;
                    padding: 16px 40px;
                    height: 48px;
                    width: 215px;
                    background: linear-gradient(90deg, #8176AF 0%, #C0B7E8 100%);
                    border-radius: 40px;
                    font-weight: 700;
                    font-size: 14px;
                    line-height: 17px;
                    color: #343045;
                    border: none;
                    cursor: pointer;
                    &:hover {
                        border: 2px solid #fff;
                        color: #fff;
                    }
                }
            }
        }
    }   
}